import React, { useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { CSSTransition } from "react-transition-group";
import ModalPortal from "../modal/ModalPortal";
import Modal from "../modal/Modal";
import Project from "./Project";

export type ProjectThumb = {
  bgImg: string;
  title: string;
};

function ProjectThumb({ bgImg, title }: ProjectThumb) {
  const [mouseHover, setMouseHover] = useState(false);
  const [viewModal, setViewModal] = useState(false);

  return (
    <div
      onClick={() => setViewModal(true)}
      onMouseOver={() => setMouseHover(true)}
      onMouseLeave={() => setMouseHover(false)}
      className="flex items-center justify-center w-64 h-64 bg-center bg-cover rounded shadow-xlr border-blue-800 hover:border-blue-700 border-4 relative m-5 cursor-pointer"
      style={{ backgroundImage: `url(${bgImg})` }}
    >
      {!mouseHover && (
        <span
          className="text-white text-2xl font-semibold absolute"
          style={{ bottom: "1rem" }}
        >
          {title}
        </span>
      )}
      <CSSTransition in={mouseHover} timeout={1} classNames="fade">
        <div
          className="w-full h-full flex justify-center items-center text-white opacity-0"
          style={{
            backgroundImage: `linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),url()`
          }}
        >
          <span
            className="text-white text-2xl font-semibold absolute"
            style={{ bottom: "1rem" }}
          >
            {title}
          </span>
          <FontAwesomeIcon icon="plus" size="3x"></FontAwesomeIcon>
        </div>
      </CSSTransition>
      {viewModal && (
        <ModalPortal>
          <Modal onClickCancel={() => setViewModal(false)}>
            <Project></Project>
          </Modal>
        </ModalPortal>
      )}
    </div>
  );
}

export default ProjectThumb;
