import React from "react";
import bgImg from "../img/bg-business.jpg";
import ProjectThumb from "./business/ProjectThumb";
function Business() {
  return (
    <>
      <div>
        <div
          className="h-64 font-semibold text-gray-100 text-2xl text-center flex items-center main-bg flex flex-col  justify-center items-center bg-cover bg-center"
          style={{
            backgroundImage: `linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),url(${bgImg})`
          }}
        >
          <div>사업소개</div>
          <div className="text-lg">
            월드베스트파트너스는 사업분야는 이렇습니다.
          </div>
        </div>

        <div className="flex flex-wrap justify-center items-center mt-5">
          <ProjectThumb bgImg={bgImg} title="프로젝트 1"></ProjectThumb>
          <ProjectThumb bgImg={bgImg} title="프로젝트 2"></ProjectThumb>
          <ProjectThumb bgImg={bgImg} title="프로젝트 3"></ProjectThumb>
          <ProjectThumb bgImg={bgImg} title="프로젝트 4"></ProjectThumb>
        </div>
      </div>
    </>
  );
}

export default Business;
