import React from "react";
import { Carousel } from "react-responsive-carousel";
import img1 from "../../img/bg-about.jpg";
import img2 from "../../img/bg-contact.jpg";
import "react-responsive-carousel/lib/styles/carousel.min.css";

function Project() {
  return (
    <div className="flex flex-col justify items-center">
      <Carousel
        className="z-50"
        autoPlay={true}
        infiniteLoop={true}
        showThumbs={false}
      >
        <div>
          <img src={img1} alt="modal" />
          <p className="legend">Legend 1</p>
        </div>
        <div>
          <img src={img2} alt="modal" />
          <p className="legend">두번째 이미지</p>
        </div>
      </Carousel>

      <div className="flex flex-col lg:text-2xl font-semibold flex-wrap justify-center items-start">
        <div className="flex flex-wrap">
          <div className="flex flex-col m-3 lg:m-5">
            <span className="text-lg lg:text-2xl">사업규모</span>
            <span className="text-base lg:text-lg">1,231,512,312원</span>
          </div>
          <div className="flex flex-col m-3 lg:m-5">
            <span className="text-lg lg:text-2xl">고객</span>
            <span className="text-base lg:text-lg">아무나</span>
          </div>
        </div>
        <div className="flex flex-col m-3 lg:m-5">
          <span className="text-lg lg:text-2xl">기간</span>
          <span className="text-base lg:text-lg">2020/03~2020/09</span>
        </div>
      </div>
    </div>
  );
}

export default Project;
