import React from "react";
import bgImg from "../../img/CEO.jpg";

function CEO() {
  return (
    <>
      <div className="flex p-5 w-full justify-center flex-col lg:flex-row">
        <div
          className="google-map-size lg:google-map-size bg-center bg-cover"
          style={{ backgroundImage: `url(${bgImg})` }}
        ></div>
        <div className="flex flex-col justify-between w-full lg:w-1/4 p-2  lg:p-5">
          <div className="font-semibold text-sm lg:text-base ">
            <blockquote>
              한국에 Long only 기반 운용사는 많지만,
              <br></br>Block-Deal과 Pre-IPO 투자를 전문으로 하는 대체투자 전문
              운용사는 아직 많지 않습니다.
              <br></br> 저희가 블록딜 시장을 개척하면서 얻은 가장 중요한
              깨달음은 모두가 가는 길이 아닌 새로운 길을 개척할 때 시장이
              놀랄만한 수익이 따라온다는 점이었습니다. <br></br>그렇기에 저희는
              블록딜 자문에서 그치지 않고 전문사모운용사로 전환하여 무한한
              개척과 성장의 선순환고리를 극대화하기로 하였습니다. 얼터너티브
              자산운용은 그동안 저희가 성공한 영역을 기반삼아, Pre-IPO, 부동산
              개발/시행 등 다른 사업영역에 있어서도 새로운 비즈니스 방식과
              구조로 시장을 개척하고자 합니다.<br></br> 각 분야 전문인력으로
              팀을 구성하여 새로운 길을 찾고, 경쟁력있는 솔루션으로 소수의
              선택이 옳았음을 증명하겠습니다.<br></br> 감사합니다.
            </blockquote>
          </div>
          <div className="font-semibold text-xl lg:text-2xl">
            (주)월드베스트파트너스 대표 유승규
          </div>
        </div>
      </div>
    </>
  );
}
export default CEO;
